@import './vendor/dicsBootstrap4.scss';

.heroText {
    font-size: 3rem;
}

.heroImage {
    width: 100%;
    height: auto;

    top: 0;
    right: 0;
}

.tile {
    height: 150px;
    text-align: center;
    cursor: pointer;
}

.swiper-container {
    width: 100%;
    padding-top: 50px;
    padding-bottom: 50px;
}

.swiper-slide {
    background-color: rgb(93, 248, 253);
    width: 200px;
    height: 300px;
}

.header-text {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 0.5em 1em;
    background-color: rgba(243, 248, 165, 0.63);
}

@media (max-width : 600px) {
    .heroText {
        font-size: 1.8rem;
    }

    .heroImage {
        width: 100%;
    }
}