$primary: #00296D;
$secondary: #EFF1F5;
$success: #00CCD4;
$info: #00BAF2;
$warning: #6902C2;
$danger: #F20061;
// $light: $gray-100 !default;
// $dark: $gray-800 !default;


// Add quicksand google font cdn link

$font-family-sans-serif: "Quicksand",
-apple-system,
BlinkMacSystemFont,
"Segoe UI",
Roboto,
"Helvetica Neue",
Arial,
"Noto Sans",
sans-serif,
"Apple Color Emoji",
"Segoe UI Emoji",
"Segoe UI Symbol",
"Noto Color Emoji";
$font-family-monospace: SFMono-Regular,
Menlo,
Monaco,
Consolas,
"Liberation Mono",
"Courier New",
monospace !default;
$font-family-base: $font-family-sans-serif;
@import '../../node_modules/bootstrap/scss/bootstrap.scss';

nav.navbar {
    color: #7C8EAC;
    font-weight: 800;
}

.navbar-brand img {
    width: 5rem;
    height: auto;
}

p {
    font-family: "open sans";
}